    background-color: #fff;
    transition: all .5s;
    display: block;
    position: fixed;
    top: 140px;
    left: 0;
    overflow: auto;
    transform: translateX(-100%);
  }

  .burger__menu.active {
    transform: translateX(0%);
  }

  .burger__open {
    width: 30px;
    height: 20px;
    z-index: 5;
    cursor: pointer;
    display: block;
    position: absolute;
    top: 20px;
    left: 30px;
  }

  .burger__open:before, .burger__open:after {
    content: "";
    width: 100%;
    height: 2px;
    transition: all 1s;
    position: absolute;
    left: 0;
  }

  .burger__open:before {
    background-color: #fff;
    top: 0;
  }

  .burger__open:after {
    background-color: #fff;
    bottom: 1px;
  }

  .burger__open span {
    width: 100%;
    height: 2px;
    background-color: #fff;
    position: absolute;
    top: 9px;
    left: 0;
  }

  .burger__open.active:before {
    background-color: #fff;
    top: 9px;
    transform: rotate(45deg);
  }

  .burger__open.active:after {
    background-color: #fff;
    bottom: 9px;
    transform: rotate(-45deg);
  }

  .burger__open.active span {
